#searchIcon, .searchList{
    width: 100%; 
    height: 59px;
    border-radius: 29px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    color: var(--text-head);
    padding: 0 10px 0 78px;
}

#searchIcon::placeholder{
    color:rgba(61,61,61, .3);
}

.cont{
    position: relative;
}
.loupe{
    position: absolute;
    left: 23px;
    top:18px;
    background-color: var(--white);
}
.dots{
    position: absolute;
    right: 23px;
    top:18px;
}

.search{
    width: 100%;
    margin-bottom: 30px;
}


.searchList{
    background-color: white;
    margin-bottom: 30px;
    min-height: 100px;
    padding-left: 0px;
}

.searchCont{
    padding: 13px 28px 21px 22px;
}

.list-content{
    padding-bottom: 20px;
}

.list{
    display: flex;
    align-items: center;
